import Link from 'next/link'
import { Button } from "@/components/ui/button"
import { ArrowRight } from "lucide-react"

export function CTA() {
    return (
        <section className="py-24">
            <div className="container">
                <div className="relative rounded-3xl bg-primary px-6 py-16 md:px-16 md:py-24 text-center overflow-hidden">
                    <div className="relative z-10 max-w-3xl mx-auto">
                        <h2 className="text-3xl md:text-5xl font-bold text-primary-foreground mb-6">
                            Ready to Accelerate Your MSP Sales?
                        </h2>
                        <p className="text-lg text-primary-foreground/90 mb-10">
                            Join the fastest-growing MSP sales platform. No credit card required.
                        </p>
                        <div className="flex flex-col sm:flex-row justify-center gap-4">
                            <Link href="/signup">
                                <Button size="lg" variant="secondary" className="h-12 px-8 text-base w-full sm:w-auto font-semibold">
                                    Start Free Trial
                                    <ArrowRight className="ml-2 h-4 w-4" />
                                </Button>
                            </Link>
                            <Link href="/contact">
                                <Button size="lg" variant="outline" className="h-12 px-8 text-base w-full sm:w-auto bg-transparent text-primary-foreground border-primary-foreground hover:bg-primary-foreground/10 hover:text-primary-foreground">
                                    Talk to Sales
                                </Button>
                            </Link>
                        </div>
                    </div>

                    {/* Decorative Circles */}
                    <div className="absolute top-0 left-0 w-64 h-64 bg-white/10 rounded-full blur-3xl -translate-x-1/2 -translate-y-1/2" />
                    <div className="absolute bottom-0 right-0 w-64 h-64 bg-white/10 rounded-full blur-3xl translate-x-1/2 translate-y-1/2" />
                </div>
            </div>
        </section>
    )
}
