import { Check, X, Shield } from "lucide-react"

export function Comparison() {
    const features = [
        "MSP-Specific Intent Data",
        "Verified Technographics",
        "AI Email Personalization",
        "Unlimited Contact Exports",
        "No-Code Integrations",
        "Dedicated Success Manager",
    ]

    return (
        <section className="py-20 bg-muted/20">
            <div className="container">
                <div className="text-center mb-16">
                    <h2 className="text-3xl font-bold mb-4">Why MSPs Choose Catalyst</h2>
                    <p className="text-muted-foreground">See how we stack up against general-purpose sales tools.</p>
                </div>

                <div className="overflow-x-auto">
                    <table className="w-full border-collapse min-w-[600px]">
                        <thead>
                            <tr>
                                <th className="text-left p-4 w-1/4">Feature</th>
                                <th className="p-4 w-1/4 text-center bg-primary/5 rounded-t-xl border-t border-x border-primary/20">
                                    <div className="flex items-center justify-center space-x-2 text-primary font-bold">
                                        <Shield className="h-5 w-5" />
                                        <span>Catalyst</span>
                                    </div>
                                </th>
                                <th className="p-4 w-1/4 text-center text-muted-foreground font-semibold">ZoomInfo</th>
                                <th className="p-4 w-1/4 text-center text-muted-foreground font-semibold">Apollo.io</th>
                            </tr>
                        </thead>
                        <tbody>
                            {features.map((feature, index) => (
                                <tr key={index} className="border-b last:border-0">
                                    <td className="p-4 font-medium">{feature}</td>

                                    {/* Catalyst */}
                                    <td className="p-4 text-center bg-primary/5 border-x border-primary/20">
                                        <div className="flex justify-center">
                                            <Check className="h-5 w-5 text-primary" />
                                        </div>
                                    </td>

                                    {/* ZoomInfo */}
                                    <td className="p-4 text-center">
                                        <div className="flex justify-center">
                                            {index === 0 || index === 3 ? (
                                                <X className="h-5 w-5 text-muted-foreground/50" />
                                            ) : (
                                                <Check className="h-5 w-5 text-muted-foreground" />
                                            )}
                                        </div>
                                    </td>

                                    {/* Apollo */}
                                    <td className="p-4 text-center">
                                        <div className="flex justify-center">
                                            {index === 0 || index === 5 ? (
                                                <X className="h-5 w-5 text-muted-foreground/50" />
                                            ) : (
                                                <Check className="h-5 w-5 text-muted-foreground" />
                                            )}
                                        </div>
                                    </td>
                                </tr>
                            ))}
                            <tr>
                                <td className="p-4"></td>
                                <td className="p-4 bg-primary/5 rounded-b-xl border-b border-x border-primary/20"></td>
                                <td className="p-4"></td>
                                <td className="p-4"></td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </section>
    )
}
