import Link from 'next/link'
import { Button } from "@/components/ui/button"
import { ArrowRight, PlayCircle, CheckCircle2 } from "lucide-react"

export function Hero() {
    return (
        <section className="relative pt-20 pb-16 md:pt-32 md:pb-24 overflow-hidden">
            <div className="container relative z-10 flex flex-col items-center text-center">
                <div className="animate-fade-in-up inline-flex items-center rounded-full border px-3 py-1 text-sm text-muted-foreground mb-8 bg-muted/50 backdrop-blur-sm">
                    <span className="flex h-2 w-2 rounded-full bg-primary mr-2"></span>
                    New: AI Voice Agent Integration
                </div>

                <h1 className="animate-fade-in-up delay-100 text-4xl md:text-6xl lg:text-7xl font-bold tracking-tight max-w-4xl mb-6 premium-gradient-text">
                    Power Your MSP Pipeline with AI-Powered Intelligence
                </h1>

                <p className="animate-fade-in-up delay-200 text-xl md:text-2xl text-muted-foreground max-w-2xl mb-10 leading-relaxed">
                    Built exclusively for Managed Service Providers. Find ideal clients, track tech stacks, and close deals faster.
                </p>

                <div className="animate-fade-in-up delay-300 flex flex-col sm:flex-row gap-4 mb-12 w-full sm:w-auto">
                    <Link href="/signup">
                        <Button size="lg" className="h-12 px-8 text-base w-full sm:w-auto shadow-lg shadow-primary/25 hover:shadow-primary/40 transition-shadow">
                            Start Free Trial
                            <ArrowRight className="ml-2 h-4 w-4" />
                        </Button>
                    </Link>
                    <Link href="/demo">
                        <Button size="lg" variant="outline" className="h-12 px-8 text-base w-full sm:w-auto backdrop-blur-sm bg-background/50 hover:bg-background/80">
                            <PlayCircle className="mr-2 h-4 w-4" />
                            Watch Demo
                        </Button>
                    </Link>
                </div>

                <div className="animate-fade-in-up delay-300 flex flex-wrap justify-center gap-x-8 gap-y-4 text-sm text-muted-foreground">
                    <div className="flex items-center">
                        <CheckCircle2 className="mr-2 h-4 w-4 text-primary" />
                        <span>10M+ Verified Contacts</span>
                    </div>
                    <div className="flex items-center">
                        <CheckCircle2 className="mr-2 h-4 w-4 text-primary" />
                        <span>98% Data Accuracy</span>
                    </div>
                    <div className="flex items-center">
                        <CheckCircle2 className="mr-2 h-4 w-4 text-primary" />
                        <span>Setup in 5 Minutes</span>
                    </div>
                </div>
            </div>

            {/* Background Gradient Effect */}
            <div className="absolute top-0 left-1/2 -translate-x-1/2 w-full h-[500px] bg-primary/20 blur-[120px] rounded-full -z-10 opacity-40 pointer-events-none animate-pulse duration-[5000ms]" />
        </section>
    )
}
