import {
    Target,
    Bot,
    BarChart3,
    ShieldCheck,
    Users,
    Database
} from "lucide-react"

export function Features() {
    const features = [
        {
            icon: Target,
            title: "MSP Prospect Discovery",
            description: "Identify high-intent leads using machine learning models trained specifically on MSP sales data."
        },
        {
            icon: Bot,
            title: "Automated Outreach",
            description: "Scale your email and LinkedIn campaigns with personalized, AI-generated messaging."
        },
        {
            icon: BarChart3,
            title: "MSP Deal Pipeline",
            description: "Manage your specific deal flow and track conversions with comprehensive dashboards."
        },
        {
            icon: ShieldCheck,
            title: "Security-First Design",
            description: "SOC 2 Type II certified platform ensuring your data remains enterprise-secure."
        },
        {
            icon: Users,
            title: "Buying Signals for MSPs",
            description: "Identify companies actively looking for IT services with real-time intent signal tracking."
        },
        {
            icon: Database,
            title: "Tech Stack Intelligence",
            description: "See what tools prospects are using (e.g., M365, Google Workspace, Datto) to tailor your pitch."
        }
    ]

    return (
        <section className="py-20 md:py-24" id="features">
            <div className="container">
                <div className="text-center max-w-3xl mx-auto mb-16">
                    <h2 className="text-3xl md:text-4xl font-bold mb-4">
                        Everything you need to scale your MSP
                    </h2>
                    <p className="text-lg text-muted-foreground">
                        Built from the ground up to solve the unique challenges of Managed Service Providers.
                    </p>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                    {features.map((feature, index) => (
                        <div
                            key={index}
                            className="group p-8 rounded-2xl border bg-card premium-card-hover"
                        >
                            <div className="h-12 w-12 rounded-lg bg-primary/10 flex items-center justify-center mb-6 group-hover:bg-primary/20 transition-colors">
                                <feature.icon className="h-6 w-6 text-primary" />
                            </div>
                            <h3 className="text-xl font-semibold mb-3">{feature.title}</h3>
                            <p className="text-muted-foreground leading-relaxed">
                                {feature.description}
                            </p>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    )
}
